import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)
export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: resolve=>{
        require(['@/pages/drag/Index.vue'], resolve)
      }
    },
    {
      path: '/test',
      name: 'Drag',
      component: resolve=>{
        require(['@/pages/Index.vue'], resolve)
      }
    }
  ]
})



