// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import config from '@/config'
import Loading from '@/components/kesion-ui/Loading'
const eva = require('eva-icons')
Vue.component('loading',Loading)
Vue.prototype.$config = config
Vue.prototype.$eva = eva
Vue.config.productionTip = false
/**
 * 消息提示
 */
Vue.prototype.$alert = top.Alert || function(msg){
  alert(msg)
}
/**
 * confirm
 */
Vue.prototype.$confirm = top.popup ? top.popup.confirm : function(msg,params){
  if(confirm(msg)){
    if(params && typeof params.determine === 'function'){
      params.determine()
    }
  }
}

Vue.prototype.$isArrayOrObject = function(data){
  let callType = Object.prototype.toString.call(data)
  let type = false
  if(callType === '[object Array]'){
      type = 'Array'
  }else if(callType  === '[object Object]'){
      type = 'Object'
  }
  return type
}
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})




